export default {
	components: {},
	data() {
		return {
			banner: [],
			album: [],
			params: {
				limit: 30,
				order: 'new',
				skip: 0
			},
			isEnd: false
		}
	},
	methods: {
		getData() {
			this.request({
				url: 'http://157.122.54.189:9088/image/v1/wallpaper/album',
				data: this.params
			})
			.then(result => {
				// console.log(result)
				if (result.res.album.length === 0) {
					this.isEnd = true
					uni.showToast({
						title: "已经没有更多数据了",
						icon: 'none'
					})
					return
				}
				
				if (this.banner.length === 0) {
					this.banner = result.res.banner
				}
				this.album = [...this.album,...result.res.album]
			
			})
		},
		handlePage() {
			if (!this.isEnd) {
				this.params.skip += this.params.limit
				this.getData()
			}
		},
		goAlbumDetail(id) {
			uni.navigateTo({
				url: `/pages/album-detail/index?id=${id}`,
			})
		}
	},
	created() {
		this.getData()
	}
	// mounted() {
	// 	uni.setNavigationBarTitle({
	// 		title: "专辑"
	// 	})
	// }
}
